import { useState } from "react";
import React from "react";
import {
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  Typography,
  Backdrop,
  CircularProgress,
  Select,
  InputLabel,
  FormControl,
  MenuItem,
  //   Select,
} from "@mui/material";
import styled from "styled-components";
import { prodUrl } from "./config";
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';


const ChooseFile = styled.input`
  margin-bottom: 10px;
`;

const Form = () => {
  const [loading, setLoading] = useState(false);
  const [group, setGroup] = useState(1)


  const handleChange = (e) => {
    setGroup(e.target.value)

  }

  // Handle Form Submit
  const handleSubmit = (e) => {
    e.preventDefault();


    var formdata = new FormData();
    console.log(e.target.pic.files[0])

    formdata.append("name", e.target.name.value);
    formdata.append("file", e.target.pic.files[0]);
    formdata.append("regNo", e.target.regno.value);
    formdata.append("group", group);
    formdata.append("position", e.target.position.value);
    formdata.append("fb", e.target.fb.value);
    formdata.append("insta", e.target.insta.value);
    formdata.append("github", e.target.github.value);
    formdata.append("linkedin", e.target.linkedin.value);

    var requestOptions = {
      method: "POST",
      body: formdata,
      redirect: "follow",
    };
    setLoading(true)
    fetch(`${prodUrl}/organisers`, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setLoading(false);
        console.log(result);
        handleClick()

      })
      .catch((error) => {
        console.log("error", error);
        handleClicker()

      });
    e.target.reset();
  };


  const [open, setOpen] = React.useState(false);
  const [opener, setOpener] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };
  const handleClicker = () => {
    setOpener(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  const handleCloseer = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpener(false);
  };

  const action = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );
  const actioner = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );
  return (


    <div>
      <Snackbar
        open={open}
        autoHideDuration={3000}
        onClose={handleClose}
        message="Added"
        action={action}
      />
      <Snackbar
        open={opener}
        autoHideDuration={3000}
        onClose={handleCloseer}
        message="Something went wrong!"
        action={actioner}
      />
      <Typography
        gutterBottom
        variant="h1"
        align="center"
        sx={{ fontFamily: "Roboto", fontWeight: "bold" }}
      >
        About
      </Typography>

      <Card sx={{ maxWidth: "450px", margin: "0 auto", padding: "20px 5px" }}>
        <CardContent>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={1}></Grid>
            <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="name"
                label="Name"
                placeholder="Enter Name"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid>

            <Grid item xs={12}>
              <ChooseFile name="pic" type="file" accept="image/*" />
              <p>(Max 1.5mb)<a href='https://tinypng.com/'> tinyPNG</a></p>
            </Grid>

            <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="regno"
                label="Reg No"
                // type={"number"}
                placeholder="Enter Registration Number"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid>

            <Grid item xs={12}>
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">Group</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={group}
                  label="Group"

                  onChange={handleChange}
                >
                  <MenuItem value={1}>Developers</MenuItem>
                  <MenuItem value={2}>Creative Head</MenuItem>
                  <MenuItem value={3}>Pub&amp;Branding</MenuItem>
                  <MenuItem value={4}>Stage Incharge</MenuItem>
                  <MenuItem value={5}>Sonabyss Incharge</MenuItem>
                  <MenuItem value={6}>Hospitality Incharge</MenuItem>
                  <MenuItem value={7}>Media Handler</MenuItem>
                  <MenuItem value={8}>Organizing Secy.</MenuItem>
                  <MenuItem value={9}>Auditor</MenuItem>
                  <MenuItem value={10}>Cosplay</MenuItem>
                </Select>
              </FormControl>
            </Grid>

            <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="position"
                label="Position"
                placeholder="Enter Your Position"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid>


            <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="fb"
                label="Facebook Profile(if any)"
                // type={"number"}
                placeholder="Enter fb link"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid> <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="insta"
                label="Insta Profile(if any)"
                // type={"number"}
                placeholder="Enter insta link"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid> <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="linkedin"
                label="Linkedin(if any)"
                // type={"number"}
                placeholder="Enter Linkedin link"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid> <Grid item xs={12}>
              <TextField
                sx={{ margin: "10px auto" }}
                name="github"
                label="Github Link(if any)"
                // type={"number"}
                placeholder="Enter Github link"
                variant="outlined"
                fullWidth
                autoComplete="off"
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
              >
                Submit
              </Button>
            </Grid>
          </form>
        </CardContent>
      </Card>
      <Backdrop
        sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={loading}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    </div>
  );
};


{/* <Button onClick={handleClick}>Open simple snackbar</Button> */ }


export default Form;
